.meme-cards {
  display: flex;
  flex-direction: column;
  // justify-content: space-around;
  align-items: center;

  .meme-card {
    width: 100vw;
    box-sizing: border-box;
    display: inline-block;
    padding-left: 30px;
    padding-right: 30px;
    padding-top: 10px;
    padding-bottom: 10px;

    .meme-card-content {
      border: 1px solid black;
      box-sizing: border-box;
      box-shadow: 6px 6px 4px rgba(0,0,0,0.5);
      border-radius: 3px;
      background-color: white;

      h3 {
      }
    }
  }
}